.insights{
    height: 70vh;
    background-color: #001F3D;
    padding: 4vh 8vw;
}

.insights__title{
    text-align: start;
    color: white;
    width: 50%;
    border-bottom: 4px white solid;
    margin-bottom: 8vh;
}

.insights__container{
    margin: 0 2vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.insights__card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 20vw;
    height: 45vh;
}

.insights__card_copper::before{
    content: "";
    top: 0;
    background-image: url(../../images/copper.jpg);
    background-size: cover;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: 0.6;
    /* background-size: 100% 100%; */
}

.insights__card_copper:hover::before {
    opacity: 0.8;
}

